.mainPage{
  
  background-image: url('../components/images/lio.jpg');
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: bottom right;
  height:100vh;
  width: 100vw;
  min-height:750px;
  min-width: 1080px;
}


.star{
  position: absolute;
  top:-100px;
  animation: fall 4s;
  height:40px;
  filter: grayscale(80%);
}
.star2{
  position: absolute;
  top:350px;
  left:900px;
  filter: grayscale(80%);
  animation: flash 3s infinite alternate;
  animation-delay:3s;
  height:40px;
}
.star3{
  position: absolute;
  top:200px;
  right:600px;
  animation: flash 1s infinite alternate;
  height:20px;
}
.star4{
  position: absolute;
  top:410px;
  left:10px;
  animation: flash 1s infinite alternate;
  height:40px;
}
.star5{
  position: absolute;
  top:400px;
  left:300px;
  animation: flash 1s infinite alternate;
  height:20px;
}
.star6{
  position: absolute;
  top:170px;
  right:100px;
  
  animation: flash 5s infinite alternate;
  height:30px;
}

.star7{
  position: absolute;
  top:200px;
  left:200px;

  animation: flash 3s infinite alternate;
  height:30px;

}
.star8{
  position: absolute;
  top:600px;
  right:10px;
  
  animation: flash 5s infinite alternate;
  height:20px;
}
.star9{
  position: absolute;
  top:250px;
  left:500px;
 
  animation: flash 5s infinite alternate;
  height:40px;
}
.starRight{
  position: absolute;
  top:-10px;

  filter: grayscale(80%);
  animation: rightStar 4s ;
  animation-delay:6s;
  height:40px;
}


@keyframes fall{

  0% {
    position: absolute;
    top:-10px;
    left:900px;
   

   }

  100% {
    position: absolute;
    top:500px;
    left:-200px;
    height:0px;
   
    }
 }
 @keyframes flash{

  0% {
    filter: grayscale(60%);

   }
   
 100% {
  filter: grayscale(100%);
   
   }
 }
 @keyframes rightStar{
  0% {
    position: absolute; 
    top: -450px;
    left:-300px;
   

   }

 100% {
  position: absolute;
  left:1000px;
   top: 600px;
   height:0px;

   }
 }